import Header from './Header'
import Sidebar from './Sidebar'

const layoutStyle = {
  margin: '0px auto',
  padding: 20,
  width: '960px',
  textAlign: 'left',
  border: '1px solid #DDD'
}

const layoutContent = {
  float: 'right',
  width: '700px'
}

const Layout = (props) => (
  <div style={layoutStyle}>
    <Header />
    <div style={layoutSidebar}>
      <Sidebar />
    </div>
    <div style={layoutContent}>
      {props.children}
    </div>
  </div>
)

export default Layout



